body {
    background-color: #F5FAFE;
}



.container {
    margin: 0 auto;
    padding: 0 15px;
    width: 1170px;
    color: #333333;
    font-family: 'Mulish', sans-serif;
    font-weight: 400;
    font-size: 12px;
    // font-family: 'Nunito',sans-serif; 
}

.link--offer {
    padding: 3px 9px;
    background-color: #EC1F46;
    color: #FFF;
    font-weight: 600;
    font-size: 10px;
    border-radius: 3px;
    font-family: 'Mulish',
    sans-serif;
}

.top {
    background-color: #F5FAFE;
    padding: 23px 0;
    display: flex;
}

.top__link--help,
.top__link--gift,
.top__link--loyality {
    color: #333333;

    span {
        margin-left: 20px;
    }

    :hover {
        border-bottom: solid 2px #000;
    }
}

.top__link--gift {
    margin-right: 100px;
}

.header__panel {
    background-color: #fff;
    padding: 45px 0;
    display: flex;
}

.search {
    position: relative;
    left: 0;
    top: 0;

    &__form {
        width: 365px;
        height: 40px;
        padding: 12px 23px;
        background-color: #F5FAFE;
        border: none;
        border-radius: 6px;
        padding: 12px 75px 13px 23px;
        font-family: 'Mulish', sans-serif;
        font-weight: 400;
        font-size: 12px;
        color: #333333;

    }
}

.search__btn {
    padding: 12px 25px;
    width: 65px;
    height: 40px;
    background: #EC1F46;
    border: none;
    border-radius: 0px 6px 6px 0px;
    position: absolute;
    right: 0px;
    cursor: pointer;

    &:hover {
        background: #970d27;
        border-radius: 6px;

        svg {
            width: 20px;
            height: 20px;
        }
    }
}

.cart {
    position: relative;
    top: 0;
    left: 0;

    span {
        color: #fff;
        background: #EC1F46;
        border-radius: 50%;
        padding: 3px 7px 4px 7px;
        position: absolute;
        left: 16px;
        bottom: 12px;
    }
}

.user {

    width: 150px;

    &__img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        cursor: pointer;
    }

    &__name {
        font-size: 14px;
    }
}

.menu {
    background-color: #0A223D;
    color: #fff;
    font-family: 'Mulish', serif;
    font-weight: 700;
    font-size: 14px;
    padding: 31px 0;

    ul {
        list-style: none;
        text-decoration: none;
    }

    a {
        color: #fff;
        font-weight: 700;
        font-size: 14px;

        &:hover {
            border-bottom: solid 2px #fff;
        }
    }
}

.track {
    margin-top: 12px;

    &__link {
        color: #0A223D;

        &:hover {
            border-bottom: 1px solid #000;
        }
    }
}

.info {
    margin-top: 43px;
}

.info__content {
    width: 728px;
}

.rating {
    display: flex;
}

.rating-item {
    width: 40px;
    height: 40px;
    background-image: url(../img/star__wait.png) ;
    background-repeat: no-repeat;
    cursor: pointer;
}

.rating-item.active {
    background-image: url(../img/Star.png);
    background-repeat: no-repeat;
    
}

.--h2 {
    font-family: 'Mulish', sans-serif;
    font-weight: 700;
    font-size: 24px;
    text-transform: uppercase;
    
}

.info__gallery {
    margin-top: 20px;
}

.content__image {
    width: 728px;
    height: 480px;
    border-radius: 8px;
}

.info__user {
    width: 350px;
    margin-left: 0 auto;
    
}

.info__user-raiting {
    background-color: #EC1F46;
    padding: 6px 6px;
    color: #FFF;
    width: 65px;
    text-align: center;
    border-radius: 4px;
}

.info__user-raiting--text {
    font-weight: 600;
    font-size: 8px;
}

.info__user-raiting--number {
    margin-top: 2px;
    font-weight: 700;
    font-size: 14px;

    span {
        font-size: 10px;
    }
}

.price {
    padding: 26px;
    margin-top: 26px;
    border-radius: 8px;
    background-color: #0A223D;
    color: #fff;
    font-family: 'Nunito',
    sans-serif;
    font-weight: 400;
    font-size: 12px;
    
    &__block-text {
        font-size: 10px;
    }

    &__block-number {
        line-height: 32px;
        font-weight: 800;
        font-size: 24px;
    }
}

.price__block-text--3 {
    margin-top: 9px;
    font-weight: 600;
    font-size: 18px;
}

.icon {
    display: block;
    margin-right: 10px;
}

.price__info-tel {
    display: block;
    margin-top: 20px;
    color: #FFF;
}